import code from "../../util/code.js"
import common from "../../util/common.js"
export default {
	data() {
		return {
			key:'',
			status:null,
			linkName:'',
		};
	},
	onLoad(option) {
		const that = this;
		that.key = option.key;
		//获取招聘信息
		uni.request({
			url: common.port.recruitmentById + '/'+option.key,
			method: "GET",
			dataType: "json",
			data: {},
			success: (result) => {
				console.log(result)
				if (result.data.code == 200) {
					that.status = result.data.data.applyStatus;
					that.linkName = result.data.data.recruitTheme;
				}else {
					uni.showToast({
						title: result.data.msg,
						icon: 'none',
						duration: 2000,
					})
				}
			}
		})
	},
	methods:{
		gotoLink:function(){
			uni.navigateTo({
				url: '/pages/notice/notice?key=' + that.key
			});
		},
		back:function(e){
			uni.navigateBack({
			    delta: 1
			});
		}
	}
}
